'use strict';

/**
 * Tooltipster Class
 */
var Tooltipster = {

  /**
   * Initializer
   */
  init: function init() {

    this.activateTooltipster();
  },

  activateTooltipster: function activateTooltipster() {

    $('.tooltipster-click').tooltipster({
      animation: 'fade',
      delay: 200,
      trigger: 'click',
      maxWidth: 350,
      timer: 1000
    });

    $('.tooltipster').tooltipster({
      'maxWidth': 350
    });
  }

};

module.exports = Tooltipster;
