"use strict";

/**
 * AutoResize Class
 */
var AutoResize = {

  /**
   * Initializer
   */
  init: function init() {

    this.setupInternational();
  },

  setupInternational: function setupInternational() {

    $("#user_mobile").intlTelInput({
      nationalMode: false,
      preferredCountries: ["DE", "CN"],
      initialCountry: "DE"
    });

    $("#inquiry_mobile").intlTelInput({
      nationalMode: false,
      preferredCountries: ["DE", "CN"],
      initialCountry: "CN"
    });

    $("#address_mobile").intlTelInput({
      nationalMode: false,
      preferredCountries: ["DE", "CN"],
      initialCountry: "CN"
    });

    $("#shop_application_mobile").intlTelInput({
      nationalMode: false,
      preferredCountries: ["DE", "CN"],
      initialCountry: "DE"
    });

    $("#shop_application_tel").intlTelInput({
      nationalMode: false,
      preferredCountries: ["DE", "CN"],
      initialCountry: "DE"
    });
  }
};

module.exports = AutoResize;
